import React from 'react'
import {View, Panel, PanelHeader, ScreenSpinner, List, Cell, Group, PullToRefresh} from '@vkontakte/vkui'

import ReportDraggingList from './Report/ReportDraggingList'

class Reports extends React.Component {
    constructor() {
        super()
        this.state = {
            activePanel: 'reports',
            reports: [],
            popout: <ScreenSpinner />,
            isFetching: false,
            reportID: null
        }
    }

    componentDidMount() {
        fetch(sessionStorage.getItem('address') + '/api/v0/allreports', {
            method: 'GET',
            headers: {
                'tehnarenok-token': sessionStorage.getItem('token')
            }
        }).then(data => {
            return data.json()
        }).then(data => {
            this.setState({reports: data.reports, popout: null})
        })
    }

    onRefresh = () => {
        this.setState({fetching: true})
        fetch(sessionStorage.getItem('address') + '/api/v0/allreports', {
            method: 'GET',
            headers: {
                'tehnarenok-token': sessionStorage.getItem('token')
            }
        }).then(data => {
            return data.json()
        }).then(data => {
            this.setState({reports: data.reports, isFetching: false})
        })
    }

    onClickReport = (reportID, type) => {
        switch (type) {
            case 'draggingList':
                this.setState({activePanel: 'reportdragginglist', reportID: reportID})
                break
            default:
                break
        }
    }

    render() {
        console.log(this.state)
        return (
            <View id={this.props.id} activePanel={this.state.activePanel} popout={this.state.popout}>
                <Panel id='reports'>
                    <PanelHeader>
                        Отчеты
                    </PanelHeader>
                    <PullToRefresh onRefresh={this.onRefresh} isFetching={this.state.fetching}>
                        <Group>
                            <List>
                                {this.state.reports && this.state.reports.length > 0 && this.state.reports.map((value) => {
                                    return (
                                        <Cell 
                                            key={value._id} 
                                            description={value.discription}
                                            onClick={() => this.onClickReport(value._id, value.structureID)}
                                        >{value.name}</Cell>
                                    )
                                })}
                            </List>
                        </Group>
                    </PullToRefresh>
                </Panel>
                <ReportDraggingList id='reportdragginglist' reportID={this.state.reportID}/>
            </View>
        )
    }
}

export default Reports